.card-page {
	&__maintenance {
		&:not(:last-child) {
			margin-bottom: 44px;
			@include sm {
				margin-bottom: 32px; } } }
	&__wind {
		&:not(:first-child) {
			margin-top: 32px;
			@include sm {
				margin-top: 39px; } } }
	&__sm-banner {
		@include sm {
			margin-bottom: 32px; } } }
.card {
	position: relative;
	padding-bottom: 5px;
	[data-mob-slider] & {
		@include sm {
			max-width: 280px; } }
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		@include hover {
			& ~ .card__title {
				color: $red; } } }
	&__img {
		position: relative;
		height: 248px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		@include md {
			height: 216px; }
		@include sm {
			height: 176px; } }
	&__badges {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		pointer-events: none;
		z-index: 2;
		padding: 20px 20px 20px 24px;
		@include sm {
			padding: 8px 8px 8px 13px; } }
	&__date {
		margin-top: 18px;
		@include md {
			margin-top: 14px; }
		@include sm {
			margin-top: 10px; } }
	&__badge {
		margin: 4px 4px 0 0;
		pointer-events: initial; }
	&__title {
		margin-top: 14px;
		max-height: 84px;
		overflow: hidden;
		transition: color .3s;
		@include f(20px,600,1.4);
		display: block;
		@include md {
			margin-top: 8px; }
		@include sm {
			margin-top: 5px;
			max-height: none;
			@include f(16px,600,1.5); }
		br {
			@include sm {
				display: none; } } }
	&__maintenance {
		&:not(:last-child) {
			margin-bottom: 37px; } } }
.card-lg {
	position: relative;
	min-width: 100%;
	color: $white;
	height: 533px;
	@include md {
		margin: 0;
		height: 450px; }
	@include sm {
		color: $black;
		height: auto; }
	&_ml {
		margin-left: -40px; }
	&_mt {
		margin-top: -40px; }
	&_mr {
		margin-right: -40px; }
	&_mb {
		margin-bottom: -40px; }
	&_middle {
		height: 376px;
		@include md {
			height: 328px; } }
	&_black {
		color: $black; }
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		@include sm {
			position: relative;
			margin-bottom: 10px;
			height: 176px; }
		&_top {
			top: -16px;
			background-position: bottom right;
			@include sm {
				top: 0; } }
		&_right {
			right: -16px;
			background-position: bottom right;
			@include sm {
				right: 0; } }
		&_left {
			left: -16px;
			@include sm {
				left: 0; } }
		&_bottom {
			right: -16px;
			@include sm {
				right: 0; } } }
	&__wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		padding: 108px 40px 40px;
		@include md {
			padding: 85px 39px 39px; }
		@include sm {
			position: static;
			padding: 0; }
		.card-lg_middle & {
			padding: 27px 40px 40px;
			@include md {
				padding: 49px 39px 39px; }
			@include sm {
				padding: 0; } } }
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		@include hover {
			& ~ .card-lg__wrap .card-lg__title {
				color: $red; } } }
	&__head {
		position: relative;
		z-index: 2;
		pointer-events: none;
		flex-wrap: wrap;
		align-items: center;
		display: flex;
		@include sm {
			position: static; }
		&:not(:last-child) {
			margin-bottom: 40px;
			@include sm {
				margin-bottom: 0; } }
		& > * {
			pointer-events: initial; }
		.card-lg_middle & {
			margin-bottom: 64px;
			@include md {
				margin-bottom: 36px; } } }
	&__badge {
		@include sm {
			position: absolute;
			top: 8px;
			left: 12px; }
		&.badge,
		.badge {
			margin: 4px 30px 0 0; } }
	&__date {
		@include sm {
			margin-bottom: 4px; } }
	&__title {
		max-width: 400px;
		max-height: 220px;
		overflow: hidden;
		@include f(32px,700,1.375);
		color: currentColor;
		transition: color .3s;
		@include md {
			font-size: 28px;
			line-height: 1.4;
			max-height: 213px; }
		@include sm {
			max-height: none;
			@include f(16px,600,1.5); }
		br {
			@include sm {
				display: none; } } } }

.card-sm {
	position: relative;
	display: flex;
	&:not(:last-child) {
		margin-bottom: 24px;
		padding-bottom: 24px;
		border-bottom: 1px solid #F2F2F2;
		@include md {
			margin-bottom: 21px;
			padding-bottom: 21px; }
		@include sm {
			margin-bottom: 17px;
			padding-bottom: 17px; } }
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		top: 0;
		bottom: 0;
		width: 100%;
		@include sm {
			top: 0; }
		@include hover {
			& ~ .card-sm__wrap .card-sm__title {
				color: $red; } } }
	&__img {
		flex-shrink: 0;
		width: 96px;
		height: 88px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		@include md {
			width: 88px;
			height: 80px; }
		@include sm {
			display: none; }
		& + .card-sm__wrap {
			padding-left: 24px;
			@include md {
				padding-left: 20px; }
			@include sm {
				padding-left: 0; } } }
	&__wrap {
		flex-grow: 1; }
	&__title {
		max-height: 66px;
		overflow: hidden;
		transition: color .3s;
		@include f(16px,600,1.375);
		@include md {
			font-size: 14px;
			line-height: 1.45; } }
	&__footer {
		position: relative;
		align-items: center;
		flex-wrap: wrap;
		display: flex; }
	&__badge {
		margin: 5px 16px 0 0;
		@include md {
			margin-top: 8px; }
		@include sm {
			margin-top: 9px; } }
	&__date {
		margin-top: 5px;
		@include md {
			margin-top: 8px; }
		@include sm {
			margin-top: 9px; } } }

.card-simple {
	&__top {
		align-items: center;
		flex-wrap: wrap;
		display: flex;
		margin-bottom: 40px;
		@include md {
			margin-top: -5px;
			margin-bottom: 30px; } }
	&__badge {
		margin: 5px 16px 0 0; }
	&__date {
		margin-top: 5px; }
	&__title {
		max-height: 160px;
		overflow: hidden;
		@include f(28px,700,1.43);
		color: currentColor;
		transition: color .3s;
		display: block;
		@include md {
			padding-right: 10px;
			font-size: 22px;
			line-height: 1.55; }
		@include hover {
			color: $red; } } }

.cardtable {
	display: flex;
	@include f(14px,400,1.7);
	@include sm {
		font-size: 13px;
		line-height: 1.54;
		align-items: center;
		flex-direction: column; }
	&:not(:last-child) {
		margin-bottom: 56px;
		@include md {
			margin-bottom: 37px; }
		@include sm {
			margin-bottom: -4px; } }
	&__img {
		display: flex;
		align-items: flex-end;
		width: 290px;
		height: 360px;
		flex-shrink: 0;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		@include md {
			width: 216px;
			height: 272px; }
		@include sm {
			margin-bottom: 32px;
			width: 280px;
			height: 328px; }
		&:not(:last-child) {
			margin-right: 40px;
			@include md {
				margin-right: 32px; }
			@include sm {
				margin-right: 0; } }
		&_ml {
			margin-left: -40px;
			@include md {
				margin-left: 0; } } }
	&__btn {
		padding: 16px;
		width: 100%;
		@include md {
			padding: 12px 16px; }
		@include sm {
			padding: 7px 9px; } }
	&__wrap {
		flex-grow: 1;
		@include sm {
			flex-grow: initial;
			width: 100%; } }
	&__field {
		display: flex;
		@include sm {
			flex-direction: column; }
		&:not(:last-child) {
			margin-bottom: 20px;
			@include md {
				margin-bottom: 10px; }
			@include sm {
				margin-bottom: 15px; } } }
	&__left {
		padding-right: 20px;
		@include fos;
		flex-shrink: 0;
		width: 190px;
		color: #A9A9B2;
		@include md {
			width: 170px; }
		@include sm {
			margin-bottom: 8px;
			width: 100%; } }
	&__right {
		flex-grow: 1; }
	&__date {
		&:not(:last-child) {
			margin-right: 38px; } }
	a {
		@include hover {
			border-color: transparent; }
		&:not([class]) {
			@include fos;
			@include f(14px,600,1.7);
			border-bottom: 1px solid #CFCFD9;
			color: currentColor;
			transition: border-color .3s;
			@include sm {
				font-size: 13px; } } } }
.mob-accordion {
	@include sm {
		padding: 22px !important;
		width: 100%;
		border: 4px solid #F6F8F9; }
	&__title {
		@include sm {
			position: relative;
			margin: 0;
			padding-right: 18px;
			cursor: pointer;
			@include f(18px,700,1.375); }
		.icon-arrow-bottom {
			position: absolute;
			top: 50%;
			right: 0;
			font-size: 8px;
			transform: translateY(-50%);
			transition: transform .5s;
			.is-open & {
				transform: translateY(-50%) rotate(180deg); } } }
	&__wrap {
		@include sm {
			padding-top: 18px; } } }

.card-def {
	&__ing {
		img {
			max-width: 100%;
			display: block; } }
	&__footer {
		margin-top: 16px; } }
