.btn {
	padding: 16px 20px;
	min-width: 240px;
	color: $red;
	@include fmt;
	@include f(16px,600,1.375);
	text-align: center;
	vertical-align: top;
	border-radius: 4px;
	border: 1px solid #CFCFD9;
	background-color: transparent;
	transition: border-color .3s;
	display: inline-block;
	@include hover {
		border-color: $red; }
	&:disabled {
		color: #CFCFD9;
		cursor: default;
		@include hover {
			border-color: #CFCFD9; } }
	&_full {
		width: 100%; } }
.btn-simple {
	padding: 14px 20px;
	min-width: 160px;
	color: $white;
	@include fgp;
	@include f(14px,500,1.43);
	text-align: center;
	border-radius: 4px;
	background-color: $red;
	transition: background-color .3s;
	vertical-align: top;
	display: inline-block;
	@include hover {
		background-color: #FB695E;
		&:active {
			background-color: #EF4A3E; } }
	&:active {
		background-color: #EF4A3E; }
	&:disabled {
		color: #A9A9B2;
		cursor: default;
		background-color: #CFCFD9;
		@include hover {
			background-color: #CFCFD9; } }
	@include md {
		min-width: 114px; }
	&_full {
		width: 100%; } }
