.persons {
	&__head {
		margin-bottom: 32px;
		@include md {
			margin-bottom: 40px; }
		@include sm {
			margin-bottom: 27px; } }
	&__row {
		margin-bottom: -36px; }
	&__col {
		margin-bottom: 36px; }
	&__footer {
		margin-top: 56px;
		@include md {
			margin-top: 4px; } }
	&__birthdays {
		margin-bottom: 40px; }
	&__menu {
		position: relative;
		z-index: 3;
		margin-bottom: 48px;
		@include md {
			margin: 0 -24px 36px;
			padding-left: 24px;
			&:before,
			&:after {
				position: absolute;
				content: '';
				top: 0;
				right: 0;
				z-index: 1;
				pointer-events: none;
				width: 76px;
				bottom: 0;
				background: linear-gradient(270deg, #F6F8F9 0%, rgba(246, 248, 249, 0) 100%); }
			&:after {
				position: absolute;
				content: '';
				left: 0;
				right: auto;
				transform: rotate(180deg); } }
		@include sm {
			margin-bottom: 20px; } }
	&__filter {
		margin: 48px 0 32px;
		@include md {
			margin: 36px 0 23px; }
		@include sm {
			margin: 20px 0 15px; } } }
.person {
	color: $black;
	@include hover {
		.person__name {
			color: $red; } }
	&__img {
		margin-bottom: 16px;
		height: 324px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		display: block;
		@include md {
			margin-bottom: 14px;
			height: 271px; }
		@include sm {
			margin-bottom: 6px;
			height: 160px; } }
	&__profession {
		@include fos;
		@include f(11px,700,1.64);
		color: #CFCFD9;
		text-transform: uppercase;
		display: block;
		@include sm {
			font-size: 10px; } }
	&__name {
		margin-top: 12px;
		@include f(18px,600,1.4);
		display: block;
		transition: color .3s ease-in-out;
		@include md {
			margin-top: 8px; }
		@include sm {
			margin-top: 2px;
			font-size: 13px; } } }

.menu {
	position: relative;
	padding: 33px 40px 35px;
	display: flex;
	flex-wrap: wrap;
	@include f(14px,600,1.43);
	background-color: #F6F8F9;
	@include lg {
		padding: 28px 20px; }
	@include md {
		font-size: 14px;
		// overflow-x: auto
		display: block; }
	@include sm {
		padding: 23px 20px;
		font-size: 13px; }
	.slick-list {
		@include md {
			overflow: visible !important; } }
	&__item {
		position: relative;
		@include md {
			padding-right: 40px;
			vertical-align: top;
			display: inline-block; }
		@include sm {
			padding-right: 36px; }
		&:not(:first-child) {
			margin-left: 33px;
			@include lg {
				margin-left: 12px; } }
		@include hover {
			.menu__link {
				color: $red; }
			.menu__btn {
				color: $red; }
			.menu__btn-points {
				background-color: $red;
				&:before,
				&:after {
					background-color: $red; } }
			.menu__list {
				@include fadein; } } }
	&__link {
		position: relative;
		// padding: 0 8px
		transition: color .3s ease-in-out;
		color: currentColor;
		display: block;
		@include hover {
			color: $red; }
		&:before {
			position: absolute;
			content: '';
			top: 100%;
			left: 0;
			right: 0;
			margin-top: 29px;
			z-index: 1;
			height: 1px;
			@include fadeout(.3s);
			background-color: $red;
			@include md {
				display: none; } }
		&:after {
			position: absolute;
			content: '';
			top: -29px;
			left: 0;
			width: 100%;
			bottom: -29px;
			@include md {
				display: none; } }
		&.is-active {
			color: $red; } }
	&__btn {
		position: relative;
		padding-right: 37px;
		@include fmt;
		color: currentColor;
		transition: color .3s ease-in-out;
		@include f(14px,600,1.43);
		background-color: transparent;
		@include hover {
			color: $red;
			.menu__btn-points {
				background-color: $red;
				&:before,
				&:after {
					background-color: $red; } } }
		&:after {
			position: absolute;
			content: '';
			top: -29px;
			left: 0;
			width: 100%;
			bottom: -29px; } }
	&__btn-points {
		position: absolute;
		top: 50%;
		right: 0;
		margin-top: -2px;
		@include circle(4);
		display: block;
		transition: background-color .3s ease-in-out;
		background-color: #DEDEDE;
		transition-delay: .1s;
		&:before,
		&:after {
			position: absolute;
			content: '';
			top: 0;
			@include circle(4);
			transition: background-color .3s ease-in-out;
			background-color: #DEDEDE; }
		&:before {
			right: 8px;
			transition-delay: .05; }
		&:after {
			right: 16px; } }
	&__list {
		position: absolute;
		top: 100%;
		left: -40px;
		margin-top: 19px;
		padding: 32px 40px;
		width: 410px;
		font-weight: 400;
		@include fadeout(.3s);
		max-height: 408px;
		overflow-y: auto;
		border-radius: 4px;
		box-shadow: 0px 12px 32px rgba(0, 0, 0, 0.16);
		background-color: $white;
		&::-webkit-scrollbar {
			width: 4px; }
		&::-webkit-scrollbar-track {
			-webkit-box-shadow: none; }
		&::-webkit-scrollbar-thumb {
			background-color: $red;
			border-radius: 2px;
			outline: none; }
		.is-open & {
			@include md {
				@include fadein; } }
		@include sm {
			margin-top: 24px;
			left: 20px;
			padding: 25px 20px;
			width: 256px;
			max-height: 288px; }
		.menu__item:last-child & {
			left: auto;
			right: -10px;
			@include md {
				display: block; }
			@include sm {
				left: 0;
				right: auto; } }
		li {
			display: block;
			&:not(:last-child) {
				margin-bottom: 20px;
				@include sm {
					margin-bottom: 15px; } } }
		a {
			color: currentColor;
			transition: color .3s;
			display: block;
			@include md {
				font-weight: 600; }
			@include hover {
				color: $red; } } } }
