.breadcrumbs {
	padding: 16px 0 32px;
	align-items: center;
	@include f(12px,400,1.67);
	display: flex;
	flex-wrap: wrap;
	@include md {
		padding: 0 0 32px;
		height: 0;
		overflow: hidden; }
	@include sm {
		padding: 0 0 26px; }
	&__item {
		position: relative;
		vertical-align: top;
		display: inline-block;
		@include md {
			display: none; }
		&:not(:last-child) {
			padding-right: 25px;
			&:before {
				position: absolute;
				content: '';
				top: 50%;
				right: 10px;
				width: 5px;
				height: 1px;
				background-color: #CFCFD9; } }
		a {
			color: #CFCFD9;
			transition: color .3s;
			@include hover {
				color: $red; } } } }
