.badge {
	padding: 3px 10px;
	color: $white;
	@include f(11px,700,1.45);
	letter-spacing: 0;
	vertical-align: top;
	display: inline-block;
	border-radius: 4px;
	transition: background-color .3s, color .3s;
	text-transform: uppercase;
	background-color: $blue;
	@include hover {
		background-color: lighten($blue, 7%); }
	&_simple {
		@include fos;
		padding: 0;
		border: none;
		color: #A9A9B2;
		background-color: transparent;
		@include hover {
			color: $red;
			background-color: transparent; } }
	&_white {
		color: rgba($white, .6);
		border: 1px solid rgba($white, .2);
		background-color: transparent;
		@include hover {
			color: $red;
			background-color: transparent; } }
	&_purple {
		background-color: #BE38D4;
		@include hover {
			background-color: lighten(#BE38D4, 7%); } }
	&_green {
		background-color: #03A536;
		@include hover {
			background-color: lighten(#03A536, 7%); } }
	&_yellow {
		background-color: #D09C17;
		@include hover {
			background-color: lighten(#D09C17, 7%); } }
	&_red {
		background-color: #F15145;
		@include hover {
			background-color: lighten(#F15145, 7%); } } }

.date {
	@include f(11px,700,1.45);
	@include fos;
	letter-spacing: 0.03em;
	opacity: .4;
	text-transform: uppercase;
	&_light {
		opacity: 1;
		color: #CFCFD9; }
	&_int {
		text-transform: initial; } }

.link {
	position: relative;
	margin: 0 -10px;
	padding: 0 45px;
	@include fmt;
	@include f(16px,700,1.375);
	color: $red;
	transition: padding-right .4s;
	vertical-align: top;
	display: inline-block;
	@include md {
		padding-right: 0;
		margin: 0; }
	&:before {
		position: absolute;
		top: 50%;
		right: 100%;
		margin-right: -48px;
		margin-top: -24px;
		content: '';
		@include circle(48);
		transition: transform .4s, right .4s, background-color .3s;
		background-color: #F6F8FC; }
	&:after {
		position: absolute;
		content: '';
		top: 50%;
		right: 100%;
		margin-top: -1px;
		margin-right: -28px;
		width: 12px;
		height: 2px;
		transition: transform .4s, right .4s, opacity .4s;
		background-color: $red; }
	@include hover {
		&:before {
			right: 48px;
			@include md {
				right: 100%; } }
		&:after {
			right: 45px;
			transform: scaleX(0);
			opacity: 0;
			@include md {
				right: 100%;
				opacity: 1;
				transform: none; } }
		.link__icon {
			transform: translateX(0) scale(1);
			transition-delay: .3s;
			@include md {
				transform: translateX(-12px) scale(0); } } }
	&:active {
		&:before {
			background-color: rgba($red, .2); } }
	&:disabled {
		color: #A9A9B2;
		cursor: default;
		&:before {
			background-color: #F6F8FC; }
		&:after {
			background-color: #A9A9B2; }
		.link__icon {
			color: #A9A9B2; }
		@include hover {
			&:before {
				right: 100%; }
			&:after {
				right: 100%;
				opacity: 1;
				transform: scale(1); }
			.link__icon {
				transform: translateX(-12px) scale(0); } } }
	&_white {
		&:before {
			background-color: $white; } }
	&__text {
		position: relative;
		z-index: 1;
		&:before {
			position: absolute;
			content: '';
			left: -45px;
			bottom: -12px;
			top: -12px;
			right: -45px; } }
	&__icon {
		position: absolute;
		right: 16px;
		top: 50%;
		margin-top: -6px;
		margin-left: 8px;
		font-size: 11px;
		transform: translateX(-12px) scale(0);
		transition: transform .2s, right .2s;
		transition-delay: 0; } }
@keyframes dashPlay {
	from {
		transform: translateX(0) scale(1); }
	to {
		transform: translateX(16px) scale(0); } }

.progress {
	background-image: linear-gradient(to right, #F15145 var(--scroll), transparent 0);
	background-repeat: no-repeat;
	position: fixed;
	width: 100%;
	height: 4px;
	z-index: 10;
	transition: background-color .3s;
	&.is-active {
		background-color: #E2E6E8; } }

.info {
	padding: 32px 40px;
	@include f(14px,400,1.7);
	background-color: #F6F8F9; }


.prestory {
	position: relative;
	padding: 29px 32px 28px 81px;
	border: 4px solid #F6F8F9;
	@include f(14px,400,1.7);
	color: currentColor;
	display: block;
	@include sm {
		padding: 17px 17px 23px 52px;
		@include f(13px,400,1.7); }
	@include hover {
		.prestory__theme {
			color: $red; } }
	&__icon {
		position: absolute;
		top: 50%;
		left: 36px;
		margin-top: -6px;
		font-size: 0;
		@include sm {
			top: 20px;
			left: 18px;
			margin-top: 0; }
		.icon {
			font-size: 12px;
			color: $red; } }
	&__text {
		margin-right: 7px;
		color: #A9A9B2; }
	&__theme {
		font-weight: 700;
		transition: color .3s;
		@include sm {
			margin-top: 5px;
			margin-left: -32px;
			font-size: 14px;
			line-height: 1.4;
			display: block; } } }

.copycontrol {
	position: relative;
	max-width: 360px;
	width: 100%;
	vertical-align: top;
	display: inline-block;
	.input input {
		color: $gray; }
	&__btn {
		position: absolute;
		top: 1px;
		right: 1px;
		width: 48px;
		height: 46px;
		line-height: 46px;
		text-align: center;
		color: #F15145;
		font-size: 0;
		border-radius: 0 4px 4px 0;
		background-color: $white;
		@include hover {
			.icon-copy__top,
			.icon-copy__bottom {
				background-color: $red; } }
		&:before {
			position: absolute;
			content: '';
			top: 0;
			right: 48px;
			width: 48px;
			height: 46px;
			background: linear-gradient(270deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%); }
		.icon-copy {
			vertical-align: middle; } } }
.icon-copy {
	position: relative;
	width: 18px;
	height: 18px;
	display: inline-block;
	&__top,
	&__bottom {
		position: absolute;
		width: 12px;
		height: 12px;
		border-radius: 1px;
		border: 1px solid #F15145;
		transition: background-color .3s; }
	&__top {
		top: 0;
		right: 0; }
	&__bottom {
		left: 0;
		bottom: 0;
		z-index: 1;
		box-shadow: 0 0 0 2px $white;
		background-color: $white; } }

.likes {
	align-items: center;
	display: flex; }
.like {
	color: $lightGray;
	transition: color .3s;
	@include hover {
		color: #03A536; }
	&:not(:first-child) {
		margin-left: 10px; }
	&_dis {
		@include rotate(180);
		@include hover {
			color: $red; } }
	.icon {
		font-size: 17px; } }

.comment-icon {
	position: relative;
	padding: 2px 4px;
	line-height: 1;
	@include fos;
	@include f(10px,700,1);
	color: $black;
	border-radius: 3px;
	background-color: $lightGray;
	transition: background-color .3s;
	@include hover {
		background-color: darken($lightGray, 10%); }
	&:before {
		position: absolute;
		content: '';
		top: 100%;
		left: 50%;
		transform: translateX(-50%);
		@include arr(5,3,$lightGray,b); } }

.birthdays {
	@include sm {
		display: none; }
	&__slider {
		position: relative;
		@include md {
			&:before {
				position: absolute;
				content: '';
				top: 0;
				right: -20px;
				bottom: 0;
				pointer-events: none;
				z-index: 1;
				width: 120px;
				background: linear-gradient(270deg, #FFFFFF 0%, rgba(246, 248, 249, 0) 100%); } } }
	&__date {
		margin-bottom: 12px;
		@include fmt;
		@include f(24px, 700, 1.33);
		@include md {
			margin-bottom: 4px;
			font-size: 20px;
			line-height: 1.4; } }
	&__description {
		@include fos;
		@include f(13px,700,1.54);
		color: #CFCFD9;
		text-transform: uppercase;
		@include md {
			font-size: 13px;
			line-height: 1.54;
			letter-spacing: 0.02em; } } }

.img {
	position: relative;
	color: $white;
	&:not(:first-child) {
		.article &,
		article & {
			margin-top: 56px; } }
	&:not(:last-child) {
		.article &,
		article & {
			margin-bottom: 56px; } }
	img {
		max-width: 100%;
		display: block; }
	&__footer {
		position: absolute;
		padding: 72px 20px 24px;
		left: 0;
		bottom: 0;
		width: 100%;
		@include fos;
		@include f(14px,400,1.42);
		background: linear-gradient(180deg, rgba(42, 42, 44, 0) 0%, rgba(#2A2A2C, .8) 100%);
		@include sm {
			padding: 35px 12px 8px;
			font-size: 12px; } } }

.images {
	margin: 0 -2px;
	flex-wrap: wrap;
	display: flex;
	@include sm {
		margin: 0;
		font-size: 0; }
	&:not(:first-child) {
		.article &,
		article & {
			margin-top: 56px;
			@include sm {
				margin-top: 32px; } } }
	&:not(:last-child) {
		.article &,
		article & {
			margin-bottom: 56px;
			@include sm {
				margin-bottom: 32px; } } }
	&__col {
		padding: 0 2px;
		width: 50%;
		@include ms {
			padding: 0;
			width: 100%; } }
	&__prev,
	&__next {
		position: absolute;
		top: 0;
		bottom: 0;
		width: 48px;
		z-index: 1;
		background: linear-gradient(90deg, rgba(42, 42, 44, 0) 0%, #2A2A2C 100%);
		opacity: 0.6;
		transition: opacity .3s;
		&:active {
			opacity: 1;
			svg {
				opacity: 1; } }
		svg {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: 12px;
			opacity: .4;
			transition: opacity .3s; } }
	&__prev {
		left: 0;
		transform: matrix(-1, 0, 0, 1, 0, 0); }
	&__next {
		right: 0; }
	.img {
		.article &,
		article & {
			margin: 0; } } }

.facts {
	padding: 32px 36px;
	@include fos;
	@include f(16px,400,2);
	background: linear-gradient(0deg, #F6F8F9, #F6F8F9);
	@include sm {
		padding: 30px 20px 36px;
		font-size: 14px;
		line-height: 1.7; }
	&__title {
		margin-bottom: 28px;
		@include sm {
			margin-bottom: 16px; } }
	&__item {
		display: block;
		&:before {
			content: '— '; }
		&:not(:first-child) {
			margin-top: 20px;
			@include sm {
				margin-top: 16px; } } } }

.sign {
	align-items: center;
	color: $black;
	@include f(14px,400,1.4);
	display: inline-flex;
	@include hover {
		.sign__text {
			border-color: transparent; } }
	&__icon {
		margin-right: 12px;
		color: #CFCFD9;
		font-size: 12px;
		line-height: 1; }
	&__text {
		font-weight: 600;
		color: currentColor;
		transition: border-color .3s;
		border-bottom: 1px solid #CFCFD9;
		display: inline; } }

.nav-list {
	@include fos;
	@include f(14px,600,1.4);
	li {
		position: relative;
		padding-left: 20px;
		display: block;
		&:not(:first-child) {
			margin-top: 22px; }
		&:before {
			position: absolute;
			content: '';
			top: 50%;
			left: 0;
			@include circle(3);
			margin-top: -1px;
			background-color: #CFCFD9; } }
	a {
		color: currentColor;
		transition: border-color .3s;
		border-bottom: 1px dashed #CFCFD9;
		@include hover {
			border-color: transparent; } } }

.ui {
	h3 {
		&:not(:first-child) {
			margin-top: 48px;
			@include sm {
				margin-top: 32px; } }
		&:not(:last-child) {
			margin-bottom: 32px;
			@include sm {
				margin-bottom: 14px; } }
		@include md {
			font-size: 20px; }
		@include sm {
			font-size: 18px; } }
	h2 {
		font-size: 24px;
		@include md {
			font-size: 20px; } }
	h4,
	h5 {
		&:not(:first-child) {
			margin-top: 28px; }
		&:not(:last-child) {
			margin-bottom: 24px; } } }
