.banner {
	position: relative;
	z-index: 1;
	padding: 64px 0 58px;
	text-align: center;
	@include md {
		padding: 38px 40px 39px; }
	[data-mob-slider] & {
		@include sm {
			padding: 20px;
			max-width: 280px; } }
	&:not(:first-child) {
		.article &,
		article & {
			margin-top: 56px;
			@include sm {
				margin-top: 32px; } } }
	&:not(:last-child) {
		.article &,
		article & {
			margin-bottom: 56px;
			@include sm {
				margin-bottom: 32px; } } }
	img {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center; }
	&__title {
		margin-bottom: 24px;
		@include fmt;
		@include f(28px,700,1.4);
		display: block;
		@include md {
			margin-bottom: 16px;
			@include f(20px,700,1.6); }
		@include sm {
			margin: 0 -20px 20px;
			line-height: 1.2; } }
	&__text {
		display: block;
		@include sm {
			line-height: 1.2; } }
	&__buttons {
		margin-top: 36px;
		display: block;
		@include md {
			margin-top: 30px; }
		.btn-simple {
			@include ms {
				width: 100%; } } }
	&__left {
		padding-left: 88px;
		padding-bottom: 10px;
		max-width: 480px;
		text-align: left;
		@include md {
			padding-left: 0;
			max-width: 330px; }
		.btn-simple {
			min-width: 280px;
			[data-mob-slider] & {
				@include sm {
					min-width: 240px; } } } } }

.sm-banner {
	min-height: 345px;
	height: 100%;
	color: $white;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	display: block;
	@include md {
		min-height: 300px; }
	[data-mob-slider] & {
		@include sm {
			max-width: 280px; } }
	@include hover {
		.sm-banner__title {
			color: $red; } }
	&_bot {
		align-items: flex-end;
		display: flex; }
	&__inner {
		padding: 32px;
		width: 100%;
		@include md {
			padding: 25px; }
		@include sm {
			padding: 25px 12px; } }
	&__badge {
		margin-bottom: 15px;
		@include md {
			margin-bottom: 7px; }
		.badge {
			margin: 0 5px 5px 0; } }
	&__title {
		text-transform: uppercase;
		max-width: 274px;
		@include f(18px,700,1.55);
		transition: color .3s;
		&:not(:last-child) {
			margin-bottom: 15px; }
		@include sm {
			font-size: 14px;
			max-width: 220px; } }
	&__text {
		@include fos;
		@include f(11px,700,1.43);
		letter-spacing: 0.04em;
		text-transform: uppercase; } }
