.article,
article {
	@include fos;
	@include f(18px,400,1.78);
	@include md {
		@include f(16px,400,1.75); }
	@include sm {
		@include f(14px,400,1.71); }
	& > p {
		&:not(:last-child) {
			margin-bottom: 24px;
			@include sm {
				margin-bottom: 20px; } } }
	h1 {
		@include sm {
			font-size: 20px; } }
	h1,
	h2 {
		&:not(:first-child) {
			margin-top: 56px;
			@include md {
				margin-top: 43px; }
			@include sm {
				margin-top: 21px; } }
		&:not(:last-child) {
			margin-bottom: 45px;
			@include md {
				margin-bottom: 32px; }
			@include sm {
				margin-bottom: 16px; } } }
	h3 {
		&:not(:first-child) {
			margin-top: 48px;
			@include sm {
				margin-top: 32px; } }
		&:not(:last-child) {
			margin-bottom: 32px;
			@include sm {
				margin-bottom: 14px; } }
		@include md {
			font-size: 20px; }
		@include sm {
			font-size: 18px; } }
	h2 {
		font-size: 24px;
		@include md {
			font-size: 20px; } }
	h4 {
		text-transform: uppercase;
		@include f(16px,600,1.5); }
	h4,
	h5 {
		&:not(:first-child) {
			margin-top: 28px; }
		&:not(:last-child) {
			margin-bottom: 24px; } }
	a:not(.link) {
		font-weight: 600;
		border-bottom: 1px solid #CFCFD9;
		@include hover {
			border-color: transparent; }
		&:not([class*="btn"]) {
			color: currentColor;
			transition: border-color .3s; }
		&.link-dashed {
			border-style: dashed; } }
	ul,
	ol {
		&:not(:first-child) {
			margin-top: 56px;
			@include sm {
				margin-top: 38px; } }
		&:not(:last-child) {
			margin-bottom: 56px;
			@include sm {
				margin-bottom: 38px; } }
		li {
			display: block;
			&:not(:first-child) {
				margin-top: 24px; }
			&:before {
				margin-right: 4px; } } }
	ul {
		li {
			display: block;
			&:before {
				content: '—'; } }
		ul {
			padding-left: 40px;
			margin-top: 24px; } }
	ol {
		counter-reset: list;
		li {
			&:before {
				margin-right: 4px;
				counter-increment: list;
				content: counter(list) ". "; } } }
	table {
		overflow-x: auto;
		min-width: 760px;
		line-height: 1.58;
		font-size: 14px;
		table-layout: fixed;
		&:not(:first-child) {
			margin-top: 56px;
			@include sm {
				margin-top: 38px; } }
		&:not(:last-child) {
			margin-bottom: 56px;
			@include sm {
				margin-bottom: 38px; } }
		th,
		td {
			padding: 14px 20px;
			text-align: left;
			vertical-align: top; }
		th,
		strong {
			font-weight: 600;
			letter-spacing: 0.03em; }
		thead {
			th {
				border-top: 1px solid #EDEDF4; } }
		tbody {
			tr {
				&:nth-child(odd) {
					td {
						background-color: #F9F9FB;
						.table_vertical & {
							background-color: transparent; } } } } }
		&.table_vertical {
			tbody {
				tr {
					&:first-child {
						td {
							border-top: 1px solid #EDEDF4; } }
					&:nth-child(odd) {
						td {
							background-color: transparent; } }
					td {
						border-bottom: 1px solid #EDEDF4;
						&:nth-child(odd) {
							background-color: #F9F9FB; } } } } } }

	.card-sm a,
	a.card-sm,
	.link {
		border: none; }
	&__wind {
		&:not(:first-child) {
			margin-top: 44px; } }
	&__row {
		align-items: flex-start;
		display: flex;
		p {
			flex-grow: 1; }
		.info-block {
			flex-shrink: 0; } } }

.table-wrap {
	overflow-x: auto;
	max-width: 100%;
	width: 100%; }
blockquote {
	position: relative;
	text-align: center;
	padding: 40px 70px;
	@include f(20px,700,1.4);
	background: linear-gradient(0deg, #F6F8F9, #F6F8F9);
	@include sm {
		padding: 30px 40px;
		@include f(16px,700,1.4); }
	&:before {
		position: absolute;
		content: '';
		top: 30px;
		left: -24px;
		width: 80px;
		height: 52px;
		background-image: url(../img/text-quotes.svg);
		background-repeat: no-repeat;
		background-size: contain;
		background-repeat: no-repeat;
		@include sm {
			width: 50px;
			height: 30px;
			left: -10px; } }
	&:not(:first-child) {
		margin-top: 64px;
		@include sm {
			margin-top: 38px; } }
	&:not(:last-child) {
		margin-bottom: 64px;
		@include sm {
			margin-bottom: 38px; } } }

.info-block {
	float: right;
	width: 253px;
	margin-left: 80px;
	padding-left: 36px;
	border-left: 4px solid #F6F8F9;
	h5 {
		margin-bottom: 16px; }
	p {
		color: #A9A9B2;
		font-size: 14px; }
	img {
		max-height: 88px;
		max-width: 100%;
		display: block;
		&:not(:last-child) {
			margin-bottom: 24px; } } }

.head-inf {
	@include f(20px,600,1.8);
	@include sm {
		font-size: 16px; }
	&:not(:last-child) {
		margin-bottom: 48px;
		@include sm {
			margin-bottom: 38px; } } }

.article-footer {
	margin-top: 67px;
	@include md {
		margin-top: 42px; }
	@include sm {
		margin-top: 35px; }
	&__line {
		height: 4px;
		background-color: #F6F8F9; }
	&__field {
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		display: flex;
		@include sm {
			flex-direction: column; }
		&:not(:first-child) {
			margin-top: 72px;
			@include md {
				margin-top: 48px; }
			@include sm {
				margin-top: 40px; } }
		&:not(:last-child) {
			margin-bottom: 72px;
			@include md {
				margin-bottom: 48px; }
			@include sm {
				margin-bottom: 40px; } } }
	&__soc {
		width: 50%;
		max-width: 368px;
		padding-right: 10px;
		@include sm {
			width: 100%;
			max-width: 100%; } }
	&__copy {
		text-align: right;
		max-width: 320px;
		width: 50%;
		@include sm {
			width: 100%;
			max-width: 100%;
			order: -1;
			margin-bottom: 28px; } }
	&__facts {
		@include sm {
			margin-left: -20px;
			margin-right: -20px; }
		&:not(:first-child) {
			margin-top: 64px;
			@include sm {
				margin-top: 40px; } } }
	&__wind {
		&:not(:first-child) {
			margin-top: 48px; }
		&:not(:last-child) {
			margin-bottom: 48px; } } }
.article-topic {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 12;
	border-bottom: 1px solid #E2E6E8;
	@include fadeout(.3s);
	pointer-events: none;
	@include md {
		display: none; }
	&.is-show {
		@include fadein; }
	&__inner {
		padding-left: 186px;
		@include md {
			padding-left: 88px; } }
	&__text {
		position: relative;
		padding: 26px 0 27px 37px;
		@include f(20px,600,1.4);
		@include text-overflow;
		pointer-events: initial;
		background-color: $white;
		&:before {
			position: absolute;
			content: '';
			top: 50%;
			left: 0;
			margin-top: -16px;
			width: 1px;
			height: 32px;
			background-color: #F2F2F2; } } }
