// GENERATE GRID EXAMPLE
@include generate_grid();

.out {
	position: relative;
	padding-top: 82px;
	overflow: hidden;
	@include sm {
		padding-top: 56px; }
	&__inner {
		@include md {
			position: relative;
			left: 0;
			transition: all .3s; }
		.is-open-menu & {
			@include md {
				left: calc(100% - 68px); }
			@include sm {
				left: calc(100% - 56px); } } } }

.inner {
	margin: 0 auto;
	padding: 0 24px;
	max-width: 1328px;
	@include sm {
		padding: 0 20px; } }
.main {
	padding-right: 60px;
	@include md {
		padding-right: 16px; } }
.sidebar {
	will-change: min-height;
	&__wrap {
		margin-right: -40px;
		padding-bottom: 40px; }
	&__inner {
		transform: translate(0, 0);
		transform: translate3d(0, 0, 0);
		will-change: position, transform; }
	&__info {
		&:not(:first-child) {
			margin-top: 40px; } }
	&__field {
		&:not(:last-child) {
			margin-bottom: 40px; } } }

.head {
	margin-bottom: 40px;
	justify-content: space-between;
	align-items: center;
	display: flex;
	@include md {
		margin-bottom: 28px; }
	&__left {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		flex: 1 1; }
	&__right {
		text-align: right;
		@include sm {
			display: none; }
		.link {
			margin-top: 8px; } }
	&__title {
		&:not(:last-child) {
			margin-right: 65px;
			@include sm {
				margin-right: 0; } } }
	&__search {
		margin-top: 2px;
		@include md {
			display: none; } }
	&__inner {
		width: 100%; }
	&__description {
		&:not(:first-child) {
			margin-top: 8px; } } }

.head-main {
	justify-content: space-between;
	align-items: center;
	display: flex;
	@include md {
		flex-direction: column;
		align-items: flex-start; }
	&__left {
		padding-right: 16px;
		flex: 1 1 auto;
		@include md {
			margin-bottom: 28px;
			@include fs(100%); } }
	&__search {
		flex: 0 0 auto;
		width: 400px;
		@include md {
			@include fs(100%);
			width: 100%; } } }

.yet {
	padding: 80px 0 44px;
	background-color: #F6F8F9;
	@include md {
		padding: 54px 0 26px; }
	@include sm {
		padding: 39px 0 26px; }
	&_bottom {
		margin-bottom: -148px;
		@include md {
			padding: 36px 0 6px;
			margin-bottom: -80px; } }
	&__title {
		margin-bottom: 40px;
		@include md {
			margin-bottom: 32px; }
		@include sm {
			margin-bottom: 27px; } } }

.tablet-hide {
	@include md {
		display: none !important; } }
.tablet-show {
	display: none;
	@include md {
		display: block; } }
.mobile-hide {
	@include sm {
		display: none !important; } }
.mobile-show {
	display: none;
	@include sm {
		display: block; } }
