.news {
	padding-top: 86px;
	@include md {
		padding-top: 65px; }
	@include sm {
		padding-top: 38px; }
	&_simple {
		padding-top: 0;
		@include md {
			padding-top: 0; }
		@include sm {
			padding-top: 0; } }
	&__topic {
		padding-bottom: 49px; }
	&__head {
		width: 66.6%;
		@include md {
			width: 100%; }
		&:not(:last-child) {
			margin-bottom: 46px;
			@include md {
				margin-bottom: 31px; } } }
	&__row {
		margin-bottom: -40px;
		@include md {
			margin-bottom: -30px; } }
	&__col {
		margin-bottom: 40px;
		@include md {
			margin-bottom: 30px; }
		@include sm {
			margin-bottom: 21px; }
		&_wind {
			margin-bottom: 55px; } }
	&__footer {
		margin-top: 64px;
		@include md {
			margin-top: 47px; }
		@include sm {
			margin-top: 40px; } } }

.text-news-wrap {
	counter-reset: list; }
.text-news {
	position: relative;
	padding-right: 14px;
	&:not(:first-child) {
		margin-top: 19px; }
	&:not(:last-child) {
		padding-bottom: 23px;
		border-bottom: 1px solid rgba($white, .12); }
	&:first-child {
		&:before {
			right: 7px; } }
	&:before {
		position: absolute;
		counter-increment: list;
		content: counter(list);
		top: 0;
		right: 0;
		@include fgp;
		@include f(72px,700,1);
		opacity: .08; }
	&__title {
		color: currentColor;
		@include f(16px,600,1.375);
		transition: color .3s;
		display: block;
		@include hover {
			color: $red; } }
	&__footer {
		margin-top: 4px;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		.badge {
			margin-top: 5px;
			&:not(:last-child) {
				margin-right: 24px; } }
		.date {
			margin-top: 5px; } } }
