.link {
  position: relative;
  margin: 0 -10px;
  padding: 0 45px;
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.375;
  color: #F15145;
  transition: padding-right .4s;
  vertical-align: top;
  display: inline-block;
}

@media only screen and (max-width: 1023px) {
  .link {
    padding-right: 0;
    margin: 0;
  }
}

.link:before {
  position: absolute;
  top: 50%;
  right: 100%;
  margin-right: -48px;
  margin-top: -24px;
  content: '';
  height: 48px;
  width: 48px;
  border-radius: 50%;
  transition: transform .4s, right .4s, background-color .3s;
  background-color: #F6F8FC;
}

.link:after {
  position: absolute;
  content: '';
  top: 50%;
  right: 100%;
  margin-top: -1px;
  margin-right: -28px;
  width: 12px;
  height: 2px;
  transition: transform .4s, right .4s, opacity .4s;
  background-color: #F15145;
}

.no-touch .link:hover:before {
  right: 48px;
}

@media only screen and (max-width: 1023px) {
  .no-touch .link:hover:before {
    right: 100%;
  }
}

.no-touch .link:hover:after {
  right: 45px;
  transform: scaleX(0);
  opacity: 0;
}

@media only screen and (max-width: 1023px) {
  .no-touch .link:hover:after {
    right: 100%;
    opacity: 1;
    transform: none;
  }
}

.no-touch .link:hover .link__icon {
  transform: translateX(0) scale(1);
  transition-delay: .3s;
}

@media only screen and (max-width: 1023px) {
  .no-touch .link:hover .link__icon {
    transform: translateX(-12px) scale(0);
  }
}

.link:active:before {
  background-color: rgba(241, 81, 69, 0.2);
}

.link:disabled {
  color: #A9A9B2;
  cursor: default;
}

.link:disabled:before {
  background-color: #F6F8FC;
}

.link:disabled:after {
  background-color: #A9A9B2;
}

.link:disabled .link__icon {
  color: #A9A9B2;
}

.no-touch .link:disabled:hover:before {
  right: 100%;
}

.no-touch .link:disabled:hover:after {
  right: 100%;
  opacity: 1;
  transform: scale(1);
}

.no-touch .link:disabled:hover .link__icon {
  transform: translateX(-12px) scale(0);
}

.link_white:before {
  background-color: #fff;
}

.link__text {
  position: relative;
  z-index: 1;
}

.link__text:before {
  position: absolute;
  content: '';
  left: -45px;
  bottom: -12px;
  top: -12px;
  right: -45px;
}

.link__icon {
  position: absolute;
  right: 16px;
  top: 50%;
  margin-top: -6px;
  margin-left: 8px;
  font-size: 11px;
  transform: translateX(-12px) scale(0);
  transition: transform .2s, right .2s;
  transition-delay: 0;
}

@keyframes dashPlay {
  from {
    transform: translateX(0) scale(1);
  }
  to {
    transform: translateX(16px) scale(0);
  }
}

