  background-color: #F15145;
}

.pagination a .icon {
  line-height: 1;
  font-size: 9px;
  vertical-align: middle;
}

.gellery-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-width: 320px;
  height: 100%;
  z-index: 101;
  padding: 24px;
  overflow: auto;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
  align-items: center;
  justify-content: center;
  display: flex;
  background-color: rgba(42, 42, 44, 0.32);
}

@media only screen and (max-width: 767px) {
  .gellery-modal {
    padding: 0;
  }
}

.gellery-modal.is-open {
  opacity: 1;
  visibility: visible;
}

.gellery-modal .gallery-slider-wrap {
  margin-bottom: 16px;
}

@media only screen and (max-width: 767px) {
  .gellery-modal .gallery-slider-wrap {
    margin: 0;
  }
}

@media only screen and (max-width: 767px) {
  .gellery-modal .gallery-nav {
    display: none;
  }
}

.gellery-modal__inner {
  width: 100%;
  max-width: 992px;
}

.gellery-modal__wrap {
  padding: 16px 16px 0;
  box-shadow: 0px 12px 32px rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  overflow: hidden;
  background-color: #fff;
}

@media only screen and (max-width: 767px) {
  .gellery-modal__wrap {
    padding: 0;
    border-radius: 0;
  }
}

.gellery-modal__btn {
  margin: 0 -16px;
  padding: 10px;
  min-width: calc(100% + 32px);
  font-family: "OpenSans", Arial, sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.43;
  border-top: 1px solid #EDEDF4;
  text-align: center;
  transition: background-color .3s;
  background-color: #F6F8F9;
  display: block;
}

@media only screen and (max-width: 767px) {
  .gellery-modal__btn {
    display: none;
  }
}

.no-touch .gellery-modal__btn:hover .gellery-modal__btn-text {
  border-color: transparent;
}

.gellery-modal__btn-text {
  transition: border-color .3s;
  border-bottom: 1px dashed #CFCFD9;
}
