.header {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 4;
	padding: 24px 0;
	width: 100%;
	@include f(16px,600,1.375);
	border-bottom: 1px solid #E2E6E8;
	box-shadow: 0 2px 12px rgba(#000, 0.12);
	background-color: $white;
	@include md {
		transition: left .3s; }
	@include sm {
		padding: 10px 0 12px; }
	.is-open-menu & {
		@include md {
			left: calc(100% - 68px); }
		@include sm {
			left: calc(100% - 56px); } }
	&__inner {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between; } }
.logo {
	position: relative;
	vertical-align: top;
	display: inline-block;
	@include sm {
		width: 88px; }
	img {
		max-width: 100%;
		display: block; }
	&__hide {
		display: none; } }

.btn-nav {
	position: relative;
	z-index: 2;
	width: 20px;
	height: 14px;
	background-color: transparent;
	display: none;
	&:before {
		position: absolute;
		content: '';
		top: -5px;
		left: -5px;
		right: -5px;
		bottom: -5px; }
	@include md {
		vertical-align: top;
		display: inline-block; }
	&__line {
		position: absolute;
		left: 0;
		width: 20px;
		height: 2px;
		border-radius: 2px;
		background-color: $black;
		&:first-child {
			top: 0; }
		&:nth-child(2) {
			top: 6px; }
		&:nth-child(3) {
			bottom: 0; } } }
.nav {
	margin: 0 30px 0 5px;
	align-items: center;
	flex-wrap: wrap;
	display: flex;
	@include md {
		position: absolute;
		top: 0;
		height: 100vh;
		right: 100%;
		margin: 0;
		padding: 28px 32px;
		overflow: auto;
		@include f(16px,600);
		color: #323232;
		width: calc(100% - 56px);
		background-color: #F6F8F9;
		display: block; }
	&:before {
		position: fixed;
		content: '';
		top: -20px;
		bottom: -20px;
		left: 0;
		width: calc(100% - 68px);
		pointer-events: none;
		box-shadow: inset -7px 0 20px rgba(#000, .06);
		opacity: 0;
		visibility: hidden;
		.is-open-menu & {
			@include md {
				transition: opacity .3s, visibility .3s;
				@include fadein;
				transition-delay: .3s; } }
		@include md {
			display: block; }
		@include sm {
			width: calc(100% - 56px); } }
	&__inner {
		@include md {
			padding: 0; } }
	&__novelty {
		position: relative;
		padding-bottom: 25px;
		margin-bottom: 25px;
		display: none;
		@include md {
			display: block; }
		&:before {
			position: absolute;
			content: '';
			left: -24px;
			bottom: 0;
			right: -24px;
			border-bottom: 1px solid #F2F2F2; } }
	&__soc {
		position: relative;
		display: none;
		padding-top: 25px;
		margin-top: 25px;
		@include md {
			display: block; }
		&:before {
			position: absolute;
			content: '';
			left: -24px;
			top: 0;
			right: -24px;
			border-bottom: 1px solid #F2F2F2; } }
	&__list {
		display: flex;
		flex-wrap: wrap;
		@include md {
			display: block; } }
	&__item {
		@include md {
			display: block; }
		&:not(:first-child) {
			margin-left: 33px;
			@include md {
				margin: 25px 0 0; } } }
	&__link {
		position: relative;
		padding: 0 8px;
		transition: color .3s ease-in-out;
		color: currentColor;
		display: block;
		@include md {
			padding: 0; }
		@include hover {
			color: $red; }
		&:before {
			position: absolute;
			content: '';
			top: 100%;
			left: 0;
			right: 0;
			margin-top: 29px;
			z-index: 1;
			height: 1px;
			@include fadeout(.3s);
			background-color: $red; }
		&:after {
			position: absolute;
			content: '';
			top: -29px;
			left: 0;
			width: 100%;
			bottom: -29px;
			@include md {
				display: none; } }
		&.is-active {
			color: $red;
			&:before {
				@include fadein;
				@include md {
					display: none; } } } }
	&__btn {
		position: relative;
		margin-left: 41px;
		padding-right: 37px;
		@include fmt;
		color: currentColor;
		transition: color .3s ease-in-out;
		@include f(16px,600,1.375);
		background-color: transparent;
		@include md {
			display: none; }
		@include hover {
			color: $red;
			.nav__btn-points {
				background-color: $red;
				&:before,
				&:after {
					background-color: $red; } } }
		&:after {
			position: absolute;
			content: '';
			top: -29px;
			left: 0;
			width: 100%;
			bottom: -29px; } }
	&__btn-points {
		position: absolute;
		top: 50%;
		right: 0;
		margin-top: -2px;
		@include circle(4);
		display: block;
		transition: background-color .3s ease-in-out;
		background-color: #DEDEDE;
		transition-delay: .1s;
		&:before,
		&:after {
			position: absolute;
			content: '';
			top: 0;
			@include circle(4);
			transition: background-color .3s ease-in-out;
			background-color: #DEDEDE; }
		&:before {
			right: 8px;
			transition-delay: .05; }
		&:after {
			right: 16px; } }
	&__wrap {
		@include hover {
			.nav__out {
				@include fadein; }
			.nav__btn {
				color: $red;
				.nav__btn-points {
					background-color: $red;
					&:before,
					&:after {
						background-color: $red; } } } } }
	&__out {
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		overflow: auto;
		height: 100vh;
		@include fadeout(.3s);
		border-top: 5px solid #F6F8F9;
		background-color: rgba($black, .32);
		@include md {
			position: relative;
			top: auto;
			height: auto;
			transition: none;
			@include fadein;
			border-top: none;
			background-color: unset; } }
	&__nav-full {
		padding: 32px 0;
		box-shadow: 0 8px 24px rgba(#000, 0.16);
		background-color: $white;
		@include md {
			padding: 0;
			box-shadow: none;
			background-color: transparent; } } }
.nav-full {
	position: relative;
	margin: 0 -20px;
	display: flex;
	flex-wrap: wrap;
	&:before {
		position: absolute;
		content: '';
		left: 0;
		bottom: 0;
		right: 0;
		height: 1px;
		background-color: $white;
		@include md {
			background-color: transparent; } }
	&__item {
		padding: 0 20px;
		@include fs(25%);
		@include md {
			@include fs(100%);
			margin: 25px 0 0; } }
	&__inner {
		padding: 27px 0 31px;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		border-bottom: 1px solid #F2F2F2;
		@include md {
			padding: 0;
			border: none; } }
	&__head {
		margin-bottom: 24px;
		@include md {
			margin-bottom: 0; } }
	&__category-title,
	&__news-title {
		color: currentColor;
		transition: color .3s ease-in-out;
		@include hover {
			color: $red; } }
	&__category-title {
		@include f(24px,700,1.33);
		@include md {
			@include f(16px,600);
			color: #323232; } }
	&__news-title {
		@include f(16px,600,1.375);
		@include md {
			display: none; } }
	&__date {
		margin-top: 12px;
		text-transform: uppercase;
		@include f(11px,700,1.009);
		color: $lightGray;
		@include md {
			display: none; } } }
.novelty {
	position: relative;
	color: currentColor;
	display: flex;
	align-items: center;
	transition: color .3s ease-in-out;
	@include hover {
		color: $red; }
	&.is-active {
		color: $red;
		&:after {
			position: absolute;
			content: '';
			left: -8px;
			right: -8px;
			bottom: -24px;
			height: 1px;
			background-color: $red;
			@include sm {
				bottom: -13px; } } }
	&__logo {
		margin-top: -3px;
		flex-shrink: 0;
		width: 30px;
		img {
			max-width: 100%;
			display: block; } }
	&__text {
		position: relative;
		margin-left: 25px;
		color: currentColor;
		@include md {
			margin-left: 16px;
			font-size: 16px; }
		&:before {
			position: absolute;
			content: '';
			top: -29px;
			left: -55px;
			right: 0;
			bottom: -29px; } } }
