.photo-page {
	padding-bottom: 96px;
	@include md {
		padding-bottom: 36px; }
	&__pagination {
		margin-top: 64px;
		@include md {
			margin-top: 48px; }
		@include sm {
			margin-top: 32px; } }
	&__wind {
		@include md {
			margin-top: 64px; }
		@include sm {
			margin-top: 40px; } } }
.photos {
	&__row {
		margin: 0 -6px -12px;
		flex-wrap: wrap;
		display: flex; }
	&__col {
		padding: 0 6px;
		margin-bottom: 12px;
		@include fs(33.3%);
		@include ms {
			@include fs(50%); } } }
.photo {
	padding-bottom: 124%;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center; }
