.soc {
	align-items: center;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	&__description {
		padding-right: 10px;
		@include f(14px,400,1.43);
		color: $gray;
		@include md {
			padding-right: 25px; }
		@include sm {
			padding-right: 15px;
			font-size: 13px; } }
	&__list {
		align-items: center;
		flex-wrap: wrap;
		display: flex; }
	&__item {
		padding: 2px 0;
		font-size: 0;
		line-height: 1;
		&:not(:last-child) {
			margin-right: 28px;
			@include md {
				margin-right: 25px; }
			@include sm {
				margin-right: 22px; } }
		a {
			color: currentColor;
			transition: color .3s;
			display: block;
			@include hover {
				color: $red;
				&:active {
					color: #EF4A3E; } }
			&:active {
				color: #EF4A3E; } }
		.icon {
			font-size: 20px; } } }
