.title-lg,
h1 {
	@include fmt;
	@include f(32px, 700, 1.44);
	@include md {
		font-size: 28px; }
	@include sm {
		font-size: 20px; } }
.title,
h2 {
	@include fmt;
	@include f(28px, 700, 1.33333);
	@include sm {
		font-size: 20px; } }
.title-md,
h3 {
	@include fmt;
	@include f(22px, 700, 1.4);
	@include sm {
		font-size: 18px; } }
.title-sm,
h4 {
	@include fmt;
	@include f(20px, 600, 1.375);
	@include sm {
		font-size: 14px; } }
.title-xs,
h5 {
	@include fmt;
	@include f(14px, 600, 1.58); }

.color-white {
	color: $white; }
.color-brown {
	color: #ba9d5d; }

.description {
	@include fos;
	@include f(13px,700,1.5);
	color: #CFCFD9;
	text-transform: uppercase; }
