.celebrities {
	padding: 56px 0 72px;
	border-bottom: 8px solid #FFEEC3;
	background-color: #F6F8F9;
	@include md {
		padding: 64px 0 69px; }
	@include sm {
		padding: 41px 0 46px;
		border-bottom: none; }
	&__head {
		margin-bottom: 56px;
		@include md {
			margin-bottom: 42px; }
		@include sm {
			margin-bottom: 21px; } } }
.celebrities-slider {
	position: relative;
	margin-right: -40px;
	white-space: nowrap;
	@include fadeout(.8s);
	@include md {
		margin: 0 auto; }
	&.slick-initialized {
		@include fadein; }
	.slick-list {
		overflow: visible;
		width: 100%; }
	&__slide {
		padding-right: 40px;
		opacity: .2;
		max-width: 400px;
		transition: .5s;
		vertical-align: top;
		display: inline-block;
		@include sm {
			padding-right: 20px;
			max-width: 240px; }
		.slick-active & {
			opacity: 1; } } }
.celebrity {
	align-items: center;
	display: flex;
	color: currentColor;
	white-space: normal;
	@include fos;
	@include f(14px,400,1.43);
	vertical-align: top;
	@include hover {
		.celebrity__title {
			color: $red; } }
	&_xs-full {
		padding: 12px 24px;
		transition: background-color .3s;
		@include hover {
			background-color: #F6F8F9;
			.celebrity__title {
				color: currentColor; } } }
	[data-tablet-slider] & {
		@include md {
			width: 280px; } }
	&__img {
		flex-shrink: 0;
		@include circle(136);
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		@include sm {
			@include circle(80); }
		.celebrity_sm & {
			@include circle(64); }
		.celebrity_xs-full & {
			@include circle(56); } }
	&__content {
		padding-left: 30px;
		@include md {
			padding-left: 28px; }
		@include sm {
			padding-left: 10px; }
		.celebrity_sm & {
			padding-left: 24px; }
		.celebrity_xs-full & {
			padding-left: 16px; } }
	&__title {
		margin-bottom: 4px;
		transition: color .3s;
		@include f(16px,600,1.25);
		.celebrity_xs-full & {
			@include sm {
				font-size: 14px; } } }
	&__profession {
		@include f(14px,400,1.42);
		.celebrity_xs-full & {
			@include sm {
				font-size: 13px; } } }
	&__text {
		height: 60px;
		@include md {
			height: 80px; }
		@include sm {
			height: 40px; } } }
