.footer {
	margin-top: 144px;
	border-top: 4px solid #F6F8F9;
	@include md {
		margin-top: 80px; }
	@include sm {
		margin-top: 55px; }
	&__top {
		padding: 56px 0;
		@include md {
			padding: 39px 0 41px; }
		@include sm {
			padding: 35px 0 35px; } }
	&__subscription {
		margin-bottom: 40px; }
	&__row-sb {
		justify-content: space-between;
		@include sm {
			margin: 0; } }
	&__col {
		&:not(:first-child) {
			@include sm {
				margin-top: 12px; } } }
	&__left,
	&__right {
		padding: 0 20px;
		width: 100%; }
	&__left {
		@include fs(45.4%);
		@include md {
			@include fs(72.5%); }
		@include sm {
			@include fs(100%);
			padding: 20px 24px;
			border-radius: 4px 4px 0 0;
			border: 1px solid #CFCFD9; } }
	&__right {
		padding: 0 20px;
		@include fs(54.6%);
		max-width: 680px;
		@include md {
			@include fs(26%); }
		@include sm {
			padding: 20px 24px;
			@include fs(100%);
			border-radius: 0 0 4px 4px;
			border: 1px solid #CFCFD9;
			border-top: none; } }
	[data-accordion-container] {
		@include sm {
			padding-top: 15px; } }
	&__title {
		margin-bottom: 24px;
		@include md {
			margin-bottom: 16px; }
		@include sm {
			position: relative;
			padding-right: 15px;
			margin-bottom: 0;
			cursor: pointer; }
		.footer__subscription & {
			@include md {
				margin-bottom: 31px; }
			@include sm {
				margin-bottom: 20px; } }
		.icon-arrow-bottom {
			font-size: 0;
			transition: transform .5s;
			.is-open & {
				@include rotate(180); }
			@include sm {
				position: absolute;
				top: 50%;
				right: 0;
				margin-top: -4px;
				font-size: 8px; } } }
	&__list {
		@include f(14px,400,1.43);
		li {
			display: block;
			&:not(:last-child) {
				margin-bottom: 16px;
				@include sm {
					margin-bottom: 12px; } } }
		a {
			color: currentColor;
			transition: color .3s;
			@include hover {
				color: $red; } } }
	&__bottom {
		padding: 33px 0 32px;
		background-color: #F6F8F9;
		@include md {
			padding: 46px 0 32px; }
		@include sm {
			padding: 32px 0; } }
	&__topic {
		justify-content: space-between;
		align-items: center;
		display: flex;
		@include f(14px,400,1.7);
		&:not(:last-child) {
			margin-bottom: 32px;
			padding-bottom: 33px;
			border-bottom: 1px solid rgba(#CFCFD9, .4);
			@include md {
				margin-bottom: 23px;
				padding-bottom: 26px; }
			@include sm {
				margin-bottom: 0;
				padding-bottom: 0;
				border-bottom: none;
				display: block; } } }
	&__info {
		padding-left: 15px;
		@include sm {
			padding-left: 0; } }
	&__copy {
		color: #A9A9B2;
		@include f(14px,400,1.7);
		@include md {
			font-size: 12px;
			line-height: 1.66; } }
	&__soc {
		margin-top: 40px; }
	.soc {
		&__item {
			&:not(:last-child) {
				@include sm {
					margin-right: 43px; } } } } }
